<template>
  <div class="container">
    <div class="mainbox">
      <div class="title">NEWS CENTER 新闻中心</div>
      <div id="swiper1" class="swiper-container">
        <div class="swiper-wrapper">
          <div v-for="item in list" :key="item" class="swiper-slide">
            <div class="l">
              <div class="t">{{ item.title }}</div>
              <div class="time">{{ item.time }}</div>
              <div class="des">{{ item.des }}</div>
            </div>
            <div class="r" :style="{backgroundImage:'url('+item.poster+')', backgroundSize:'100% 100%', backgroundRepeat: 'no-repeat',backgroundPosition: 'center'}" @click="govideo(item)">
              <div class="mask"></div>
              <img src="@/assets/photo/play.png" alt="">
            </div>
          </div>
        </div>
        <div class="swiper-pagination"></div>
        <div class="swiper-button-prev"></div>
        <div class="swiper-button-next"></div>
      </div>
    </div>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import Swiper from "swiper";
import data from './data.ts'
export default {
  components: {},
  data () {
    return {
      swiper: null,
      list: data.list
    }
  },
  computed: {},
  watch: {},
  created () {
  },
  mounted () {
    this.initSwiper()
  },
  methods: {
    govideo (item) {
      window.open(item.url, '_blank')
    },
    initSwiper () {
      this.swiper = new Swiper('#swiper1', {

        mousewheel: true,
        keyboard: {
          enabled: true,
          onlyInViewport: false
        },
        pagination: {
          el: '.swiper-pagination',
          clickable: true
        },
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        forceToAxis: true,
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        on: {
          init: function () {
            window.swiperAnimateCache(this) // 隐藏动画元素
            window.swiperAnimate(this) // 初始化完成开始动画
          },
          slideChangeTransitionEnd: function () {
            window.swiperAnimate(this)
          }
        }
      })
    }
  }
}
</script>
