<template>
  <div class="container">
    <div id="swiper2" class="swiper-container">
      <div class="title">NEWS CENTER 新闻中心</div>
      <div class="swiper-wrapper">
        <div v-for="item in list" :key="item" class="swiper-slide">
          <div class="t" :style="{backgroundImage:'url('+item.poster+')', backgroundSize:'100% 100%', backgroundRepeat: 'no-repeat',backgroundPosition: 'center'}" @click="govideo(item)">
            <div class="mask"></div>
            <img src="@/assets/photo/play.png" alt="">
          </div>
          <div class="b">
            <div class="_t">{{ item.title }}</div>
            <div class="time">{{ item.time }}</div>
            <div class="des">{{ item.des }}</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "swiper/dist/css/swiper.css";
import Swiper from "swiper";
import data from './data.ts'

export default {
  components: {},
  data () {
    return {
      swiper: null,
      list: data.list

    }
  },
  computed: {},
  watch: {},
  created () {
  },
  mounted () {
    this.initSwiper()
  },
  beforeUnmount () {
    this.swiper.destroy()
  },
  methods: {
    govideo (item) {
      window.open(item.url, '_blank')
    },
    initSwiper () {
      this.swiper = new Swiper('#swiper2', {
        direction: 'vertical',
        mousewheel: true,
        keyboard: {
          enabled: true,
          onlyInViewport: false
        },
        forceToAxis: true,
        observer: true, // 修改swiper自己或子元素时，自动初始化swiper
        observeParents: true, // 修改swiper的父元素时，自动初始化swiper
        on: {
          init: function () {
            window.swiperAnimateCache(this) // 隐藏动画元素
            window.swiperAnimate(this) // 初始化完成开始动画
          },
          slideChangeTransitionEnd: function () {
            window.swiperAnimate(this)
          }
        }
      })
    }
  }
}
</script>
